<template>
  <page-title/>

  <div class="panel">

    <div class="mt-3 table-responsive ">
      <table class="table">
        <thead>
        <tr>
          <th>سوال</th>
          <th>پاسخ</th>
          <th>تاریخ ثبت</th>
          <th>&</th>
        </tr>
        </thead>

        <tbody>
        <tr v-for="x in items" :key="x.qnAId">
          <td>{{ x.question }}</td>
          <td>{{ x.answer }}</td>
          <td>{{ $filters.localDate(x.createdDate) }}</td>
          <td>
            <router-link :to="`/qna/${x.qnAId}/edit`" class="btn btn-primary btn-sm">
              <span class="fa fa-edit me-2"></span>
              ویرایش
            </router-link>
          </td>
        </tr>
        </tbody>
      </table>
    </div>

  </div>
</template>

<script>
import PageTitle from '../../tools/PageTitle'

export default {
  name: 'Questions',
  components: { PageTitle },
  data: () => ({
    items: {}
  }),
  methods: {
    retrieveQNAs(page = 1) {
      this.$fetch('/qna', 'get', {
        pageNumber: page,
        pageSize: 10,
      })
          .then(x => {
            this.items = x.data
          })

    }
  },
  mounted() {
    this.retrieveQNAs()
  }
}
</script>

